<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div @click="clickFirstPage">回到DemoPage1</div>
     <div @click="clickFourthPage">回到DemoPage4</div>
  </div>
</template>

<script>
import { Router } from '../../core/router/Router'
document.title = 'DemoPage3'
export default {
  name: "DemoPage3",
  data() {
    return {
      msg: "I am DemoPage3!"
    };
  },
  methods: {
    clickFirstPage() {
       Router.navigate('/demo/page1')
    },

    clickFourthPage() {
       Router.navigate('/demo/page4')
    }
  }

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
</style>



